}

.lightbox-basic .btn-outline-reg.mfp-close.as-button:hover {
    background-color: #252c38;
    color: #ffffff;
}

/* end of back button */

/* Close X Button */
.lightbox-basic button.mfp-close.x-button {
    position: absolute;
    top: -2px;
    right: -2px;
    width: 44px;
    height: 44px;
    color: #555555;
}

