/* Well set some colors and center everything. */

.event-heading {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 4em;
}

/* We set the position to relative so that we can stack a blurred pseudo element on top of the original text */

.event-heading span {
    position: relative;
    font-family: Jura, sans-serif;
    font-weight: 900;
    font-size: 3em;
    text-transform: uppercase;
    letter-spacing: 0.05em;
    display: inline-block;
    color: #056392;
}

/* We create a pseudo element and blur it using the SVG filter. Well grab the content from the custom HTML attribute. */

.event-heading span:before {
    position: absolute;
    left: 0;
    top: 0;
    content: attr(filter-content);
    filter: url(#motion-blur-filter);
}

/* We hide the SVG filter element from the DOM as it would take up some space */

.event-heading svg {
    display: none;
}

.event-page h1 {
    font-size: 2.5em;
    font-weight: bold;
}

@media (max-width: 800px) {
    .event-heading {
        margin-bottom: 0em;
    }

    .event-heading span {
        font-size: 2em;
    }

    .event-page h1 {
        font-size: 1.5em !important;
        font-weight: bold;
    }
}
